header {
    background-color: #f5ba13;
    margin: auto -16px;
    padding: 16px 32px;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.3);
    font-family: "Montserrat", sans-serif;
    display: flex;
    justify-content: space-between;
    align-items: center;
    color: #fff;

    h1 {
        flex: 1;
        color: #fff;
        font-family: "McLaren", cursive;
        font-weight: 200;
        margin: 0px;
    }
}
